nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #1b2430;
  padding: 10px 80px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
}
#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}
#navbar li {
  list-style: none;
  padding: 0 10px;
  position: relative;
}
#navbar li a {
  text-decoration: none;
  font-size: 1rem;
  font-weight: 600;
  color: #fff;
  transition: 0.3s ease ease-in-out;
}
#navbar li a:hover,
#navbar li a.active {
  color: #17cf97;
}
#navbar li a:hover::after,
#navbar li a.active::after {
  content: "";
  width: 30%;
  height: 2px;
  background-color: #17cf97;
  position: absolute;
  bottom: -4px;
  left: 20px;
}
#mobile i {
  color: #fff;
}
